.close-icon {
	padding: 6px;
	width: 18px;
	height: 18px;
	color: $close-icon-color;
	cursor: pointer;
	transition: .3s linear;
	box-sizing: content-box;

	&:hover {
		color: $close-icon-color-hover; }

	svg {
		width: 100%;
		height: 100%; } }

.navigation-arrows {
	width: auto;
	min-width: 88px;
	position: relative;
	display: inline-flex;
	align-items: center;
	justify-content: space-between;

	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		width: 60px;
		height: 60px;
		transform-origin: 50%;
		color: $black;
		transition: transform .3s linear;
		border-radius: 50%;
		border: 1px solid $orange;

		&:hover {
			color: $orange;

			svg {
				stroke: $orange; } }

		svg {
			width: 20px;
			height: 20px;
			fill: transparent;
			stroke-width: 2px; }

		&:focus {
			outline: none; }

		&:first-child {
			margin-right: 20px;

			&:hover {
				svg {
					transform: translateX(-5px); } } }

		&:last-child {

			&:hover {
				svg {
					transform: translateX(5px); } } } }

	&--white {
		svg {
			stroke: $white; } }

	&--white-bor {
		.navigation-arrows__item {
			border-color: $white;

			&:hover {
				border-color: $orange; } }
		svg {
			stroke: $white; } } }






.navigation-arrows__items-center {
	.navigation-arrows__item {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		z-index: 1;
		width: 48px;
		height: 48px;
		border: 0;
		background: rgba(0, 0, 0, 0.3);
		svg {
			width: 16px;
			height: 16px;
			stroke: $white;
			stroke-width: 2px; } }
	.navigation-arrows__item.prev {
		left: 15px; }
	.navigation-arrows__item.next {
		right: 15px; } }


.swiper-pagination.swiper-pagination-center {
	.swiper-pagination ::v-deep {
		.swiper-pagination-bullet {
			opacity: 1;
			background: green; }
		.swiper-pagination-bullet-active {
			background-color: white; } } }
