.placement {
    .types-placement {
        margin: 27px 0 90px 0;
        &__list {
            margin: 0 -30px;
            padding: 0;
            @include flex_center_v;
            li {
                margin: 0;
                padding: 0 30px;
                &:after {
                    display: none; } } }
        &__link {
            font-size: 18px;
            line-height: 160%;
            color: $gray4;
            padding-bottom: 10px; }
        .types-placement__link:hover,
        .types-placement__link.active {
            border-bottom: 3px solid $orange;
            color: $baseTxt; } } }

.placement__types-heading {
    display: flex;
    border-bottom: 1px solid $gray3;
    margin-bottom: 65px;
    flex-wrap: wrap;
    .col {
        width: 50%;
        @include bp(1024px) {
            width: 100%; }
        h3 {
            margin-top: 0;
            display: inline-block;
            svg {
                display: inline-block;
                width: 10px;
                height: 10px;
                float: right;
                stroke: transparent;
                fill: url(#paint0_linear) $orange;
                margin: 17px 0px 0px 2px;
                &:last-child {
                    margin-left: 13px; } } }
        p {
            margin: 0 0 35px 0;
            color: $gray4;
            font-size: 16px;
            line-height: 140%;
            @include bp(1024px) {
                margin-bottom: 15px; } } } }

.placement {
    &__block {
        display: flex;
        margin-bottom: 80px;
        @include bp(1024px) {
            flex-wrap: wrap; } }

    &__slider {
        width: 58%;
        margin-bottom: 30px;
        @include bp(1024px) {
            width: 100%; }
        &-item {
            &__block {
                position: relative; }
            &__label {
                @include flex_center_b;
                position: absolute;
                top: 0;
                right: 0;
                margin: 15px;
                background: rgba(0, 0, 0, 0.5);
                z-index: 10;
                p {
                    margin: 7.25px 18px 7.25px 0;
                    color: $white;
                    line-height: 22.4px;
                    font-size: 16px;
                    font-weight: 400; }

                svg {
                    display: inline-block;
                    width: 24px;
                    height: 13px;
                    stroke: $white;
                    stroke-width: 1px;
                    stroke-opacity: 0;
                    margin: 0 9.5px 0 18px; } } } }

    &__desc {
        width: 42%;
        padding-left: 70px;
        @include bp(1050px) {
            padding-left: 45px; }
        @include bp(1024px) {
            width: 100%;
            padding-left: 0; } } }


.desc {
    &__top {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 15px;
        h3 {
            margin: 5px 50px 10px 0; } }
    p {
        font-size: 16px;
        line-height: 140%; }
    .placement__bedsize {
        margin-top: 20px;
        svg {
            width: 35px;
            height: 24px;
            stroke-width: 1px;
            margin-right: 10px; }
        .disabled {
            display: inline-block;
            position: relative;
            opacity: 0.5;
            &:after {
                position: absolute;
                top: -20px;
                right: -7px;
                content: '?';
                width: 18px;
                height: 18px;
                background-color: $gray4;
                border-radius: 50%;
                @include flex_center_b;
                font-size: 10px;
                color: $white; } } }
    p {
        margin: 0;
        line-height: 22.4px; }
    .btn {
        margin: 0 40px 0 0; }
    .link {
        margin-top: 15px; } }


.amenities {
    margin: 30px 0 30px 0;
    &__list {
        margin: 0 -5px;
        padding: 0;
        @include flex_center_v;
        flex-wrap: wrap;

        li {
            margin: 0 0 16px 0!important;
            padding: 0 5px;
            @include flex_center_v;
            width: 50%;
            &:after {
                display: none; }
            svg {
                width: 24px;
                height: 24px;
                stroke-width: 1px;
                stroke: $baseTxt;
                margin-right: 15px; }
            p {
                margin: 0;
                line-height: 140%; } } } }



