h1 {
	font-size: $h1-fs;
	line-height: $h1-lh;
	letter-spacing: 0.01em;
	margin: 0 0 round($baseGutter*2.5);

	@include bp($point-3) {
		font-size: $h1-fs-768; } }

h2 {
	font-size: $h2-fs;
	line-height: $h2-lh;
	letter-spacing: 0.01em;
	font-feature-settings: 'kern' off, 'liga' off;
	margin: round($baseGutter*2.3) 0 round($baseGutter*1.3); }

h3 {
	font-size: $h3-fs;
	line-height: $h3-lh;
	margin: round($baseGutter*2.2) 0 round($baseGutter*0.9); }

h4 {
	font-size: $h4-fs;
	line-height: $h4-lh;
	letter-spacing: -0.01em;
	margin: round($baseGutter*2.2) 0 round($baseGutter*0.8); }

h5 {
	font-size: $h5-fs;
	line-height: $h5-lh;
	margin: round($baseGutter*1.9) 0 round($baseGutter*0.5); }

h6 {
	font-size: $h6-fs;
	line-height: $h6-lh;
	margin: round($baseGutter*1.7) 0 round($baseGutter*0.6); }

p {
	font-size: $p-fs;
	line-height: $p-lh;
	margin: round($baseGutter/1.1) 0;

	&+img {
		margin: round($baseGutter*0.2) 0 0; } }

ul li,
ol, li {
	font-size: $p-fs;
	line-height: $p-lh; }


img {
	&+h1,
	&+h2,
	&+h3,
	&+h4,
	&+h5,
	&+h6,
	&+p,
	&+ul,
	&+ol {
		margin-top: round($baseGutter*1.3); }

	&+small {
		display: block;
		text-align: center;
		margin: round($baseGutter*0.333) 0 round($baseGutter*1.5); } }

small {
	font-size: $small-fs;
	line-height: $small-lh;
	font-weight: 600; }

blockquote {
	font-size: $blockquote-fs;
	line-height: $blockquote-lh;
	color: #4263EB;
	text-align: center;
	margin: round($baseGutter*2) round($baseGutter*1.3) round($baseGutter*2);
	font-weight: normal;

	p {
		font-weight: inherit;
		font-size: 1.778em;
		line-height: 150%; } }

ul,ol {
	margin: $baseGutter 0 $baseGutter 5px;

	li {
		position: relative;
		margin: round($baseGutter*0.89) 0;
		&:first-child {
			margin-top: 0; }
		&:last-child {
			margin-bottom: 0; } }

	&+img {
		margin: round($baseGutter*0.2) 0 0; } }

ol {
	list-style-type: none;
	counter-reset: item;
	li {
		&:after {
			content: counters(item, ".")". ";
			counter-increment: item;
			position: absolute;
			left: -28px;
			top: 0;
			font-size: $p-fs;
			line-height: $p-lh;
			color: $list-item-color; } } }

ul {
	li {
		&:after {
			content: '';
			position: absolute;
			left: -28px;
			top: 10px;
			width: 8px;
			height: 8px;
			border-radius: 50%;
			background-color: $list-item-color; } } }

.table-wrap {
	overflow-x: auto;
	width: 100%;
	margin: round($baseGutter*1.3) 0; }

table {
	table-layout: fixed;
	min-width: 728px;
	width: 100%;
	th {
		height: 90px;
		max-width: 176px;
		padding: 9px; }
	td {
		height: 64px; }
	td,tr,th {
		border: 1px solid #ddd;
		font-size: $small-fs;
		text-align: center; } }

h1,
h2,
h3,
h4,
h5,
h6 {
	&+p {
		margin-top: 0; } }

ul,ol,blockquote {
	h1,
	h2,
	h3,
	h4,
	h5,
	h6,
	p {
		margin: 0; } }

@include bp($point-2) {
	h1 {
		font-size: $h1-fs-480;
		line-height: $h1-lh-480;
		margin: 0 0 $baseGutter; }

	h2 {
		font-size: $h2-fs-480;
		line-height: $h2-lh-480;
		margin: $baseGutter 0 round($baseGutter*0.6); }

	h3 {
		font-size: $h3-fs-480;
		line-height: $h3-lh-480;
		margin: $baseGutter 0 round($baseGutter*0.6); }

	h4 {
		font-size: $h4-fs-480;
		line-height: $h4-lh-480;
		margin: $baseGutter 0 round($baseGutter*0.6); }

	h5 {
		font-size: $h5-fs-480;
		line-height: $h5-lh-480;
		margin: $baseGutter 0 round($baseGutter*0.6); }

	h6 {
		font-size: $h6-fs-480;
		line-height: $h6-lh-480;
		margin: $baseGutter 0 round($baseGutter*0.6); }

	p {
		font-size: $p-fs-480;
		line-height: $p-lh-480;
		margin: round($baseGutter*0.65) 0;

		&+img {
			margin: round($baseGutter*0.2) 0 0; } }

	blockquote {
		font-size: $small-fs;
		margin: round($baseGutter*0.9) round($baseGutter*0.35); }

	ul {
		li {
			&:after {
				left: -27px;
				width: 7px;
				height: 7px;
				top: 9px; } } }

	ol {
		li {
			&:after {
				font-size: $p-fs-480;
				line-height: $p-lh-480; } } }

	ul,ol {
		margin: round($baseGutter*0.65) 0;
		li {
			font-size: $p-fs-480;
			line-height: $p-lh-480;
			margin: round($baseGutter*0.65) 0;
			&:not(:last-child) {
				margin: round($baseGutter*0.65) 0; } }

		&+img {
			margin: round($baseGutter*0.2) 0 0; } }

	img {
		&+h1,
		&+h2,
		&+h3,
		&+h4,
		&+h5,
		&+h6,
		&+p,
		&+ul,
		&+ol {
			margin-top: round($baseGutter*0.7); }

		&+small {
			display: block;
			text-align: center;
			margin: round($baseGutter*0.22) 0 $baseGutter; } }

	.table-wrap {
		margin: round($baseGutter*1.14) 0; } }

