.title-decor {
	margin-top: 0;
	font-size: rem(130px);
	line-height: 80%;
	color: white;
	background-image: url(../images/mask-title.jpg);
	background-repeat: no-repeat;
	background-position: 25% 65%;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	margin-bottom: 62px;

	@include bp($point-4) {
		font-size: rem(90px); }

	@include bp($point-2) {
		font-size: rem(48px);
		line-height: 100%;
		background-position: 30% 60%; } }

.numeral {
	font-size: rem(60px);
	line-height: 100%;
	color: $orange;
	font-weight: 700;
	position: relative;

	&--line-bottom {
		padding-bottom: 20px;
		&:after {
			content: '';
			width: 35px;
			height: 2px;
			background-color: $baseTxt;
			position: absolute;
			bottom: 0;
			left: 0; } }

	&--flex {
		display: flex; }
	&__small {
		font-size: rem(36px);
		line-height: 0.8;
		margin-left: 14px; }

	@include bp($point-4) {
		&__small {
			font-size: rem(30px);
			padding-top: 3px; }
		&--line-bottom {
			&:after {
				height: 3px; } } }

	@include bp($point-2) {
		font-size: rem(24px);

		&--line-bottom {
			font-size: rem(48px); }
		&__small {
			font-size: rem(24px); } } }

.title-line {
	padding-bottom: 11px;
	position: relative;
	&:after {
		content: '';
		width: 35px;
		height: 2px;
		background-color: $orange;
		position: absolute;
		left: 0;
		bottom: 0; } }

.title-border {
	display: inline-block;
	line-height: 120%;
	border-bottom: 2px solid $orange; }

.title-large {
	h2 {
		font-size: rem(130px);
		line-height: 80%;
		margin: 0;

		@include bp(1400px) {
			font-size: 10.1vw; }

		@include bp($point-2) {
			font-size: 11vw; } } }
