* {
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	-moz-tap-highlight-color: rgba(0, 0, 0, 0);
	box-sizing: border-box; }

*::before,
*::after {
	box-sizing: inherit; }

html, :root {
	font-size: $baseRem;
	overflow-x: hidden; }

body {
	font-family: $baseFont;
	font-size: $baseRem;
	font-weight: 500;
	color: $baseTxt;
	min-width: 320px;
	padding: 0;
	margin: 0;
	overflow-x: hidden;
	height: 100%;
	@include bp(1024px) {
		-webkit-text-size-adjust: 100%; }

	::-moz-selection {
		background: #f4f2f0; }
	::selection {
		background: #f4f2f0; } }

.page {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	width: 100%;
	overflow: hidden; }

.main {
	flex-grow: 1; }

.main-top {
	padding-top: 88px;

	@include bp($point-2) {
		padding-top: 64px; } }

img {
	max-width: 100%; }

a {
	text-decoration: none;
	outline: none;
	color: inherit; }

input,
button,
select,
textarea {
	border: none;
	background-color: transparent;
	outline: none;
	cursor: pointer; }



ul, li {
	list-style: none; }

a:active, a:visited, a:focus {
	outline: none; }

.wrapper {
	width: 100%;
	min-width: 320px;
	max-width: 1300px;
	padding: 0 20px;
	margin: 0 auto;

	&--medium {
		max-width: 1420px; }

	&--large {
		max-width: 1640px; }

	&--line {
		max-width: 100%;
		padding: 0 40px; }

	@include bp(1640px) {
		&--line {
			padding: 0 20px; } }

	@include bp($point-4) {
		padding: 0 55px; }

	@include bp(700px) {
		padding: 0 20px; } }





.orange {
	background-color: $orange; }

.light-orange-gradient {
	background-color: $lightOrange; }

.yellow {
	background: $yellow; }

.beige {
	background-color: $beige; }

.green {
	background-color: $green; }

.gray1 {
	background-color: $gray1; }

.gray2 {
	background-color: $gray2; }

.gray3 {
	background-color: $gray3; }

.gray4 {
	background-color: $gray4; }

.black {
	background-color: $black; }
