.form {
	&--login {
		margin-top: 40px; }

	&-block {
		h2 {
			margin-top: 0; }

		&--login {
			margin-top: 45px;
			padding-top: 65px;
			padding-bottom: 65px;
			padding-left: 65px;
			padding-right: 195px;
			border: 7px solid $gray1;

			& h5 {
				margin-top: 0; }

			@include bp($point-4) {
				padding-top: 50px;
				padding-bottom: 50px;
				padding-left: 30px;
				padding-right: 30px; } }

		&--guest {
			padding-right: 94px;
			@include bp($point-4) {
				padding-right: 30px; } }

		&--transport {
			padding-right: 440px;
			@include bp($point-5) {
				padding-right: 65px; }
			@include bp($point-4) {
				padding-top: 50px;
				padding-bottom: 50px;
				padding-left: 30px;
				padding-right: 30px; } }

		&--mailing {
			margin-bottom: 70px;
			padding-right: 400px;
			@include bp($point-4) {
				padding-top: 50px;
				padding-bottom: 50px;
				padding-left: 30px;
				padding-right: 30px; } } }

	&-row {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		&--login-bottom {
			justify-content: start; }

		&--guest {
			align-items: baseline; } }

	&-col {
		width: 32%;
		&--login-first {
			margin-right: 2%;
			@include bp(+600px) {
				margin-right: 0; } }

		&--guest {
			width: 29%; }

		&--transport {
			width: 43%; }

		&--mailing {
			@include flex_center_v;
			width: auto;
			min-height: 36px;
			padding-left: 90px; }

		@include bp(+600px) {
			width: 100%; } }

	&-line {
		width: 100%; }

	&-bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;

		&--guest {
			justify-content: start; }

		@include bp(600px) {
			flex-direction: column;

			.btn {
				width: 100%; }

			.form-checkbox {
				margin-bottom: 20px; } } }

	&-text {
		margin: 0;
		color: $orange;
		cursor: pointer;
		@include bp($point-4) {
			min-width: 200px;
			margin-top: -20px;
			margin-bottom: 30px; }
		@include bp(600px) {
			margin-top: -10px; }
		&--bottom {
			position: relative;
			margin-left: 20%;
			line-height: 1;
			@include bp($point-4) {
				margin-top: 0;
				margin-bottom: 0; }
			@include bp(600px) {
				margin-top: 20px; }
			&::before {
				content: '';
				position: absolute;
				left: -15px;
				top: 0;
				width: 2px;
				height: 100%;
				background-color: $orange; }
			&::after {
				content: '';
				position: absolute;
				left: -15px;
				top: 0;
				width: 2px;
				height: 100%;
				background-color: $orange;
				transform: rotate(90deg); } } } }
