.left-indent {
	padding-left: 8%;

	@include bp(1400px) {
		padding-left: 0; } }

.about {
	padding: 120px 0 55px;

	&__part {
		display: flex;
		justify-content: space-between; }

	&__desc {
		max-width: 500px;
		margin-right: 0px;
		padding-top: 20px;

		h6 {
			font-weight: 500; } }

	&__desc-unit {
		max-width: 338px; }

	@include bp($point-5) {
		&__desc {
			padding-top: 0; } }

	@include bp($point-4) {
		padding-top: 90px;
		padding-bottom: 40px;
		&__part {
			display: block; }

		&__desc {
			max-width: 100%;
			display: flex;
			justify-content: space-between;
			padding-bottom: 20px;

			h2 {
				margin-bottom: 0; }

			h6 {
				margin-top: 20px; } }
		&__desc-unit {
			margin-left: 30px; } }

	@include bp(700px) {
		padding: 60px 0 30px;
		&__desc {
			display: block;
			h2 {
				max-width: 300px; } }
		&__desc-unit {
			margin-left: 0;
			margin-top: 30px; } }

	@include bp($point-2) {
		&__desc {
			h2 {
				max-width: 200px; } } } }



.services {
	padding: 55px 0;
	&-slider {
		overflow: visible;
		margin-top: 45px;
		min-width: 1500px; }

	&-block {
		display: block;
		color: $baseTxt;
		border: none;
		transition: 0s;

		h6 {
			margin: 17px 0;
			font-weight: 500;
			transition: 0.3s; }

		&__image {
			overflow: hidden;
			img {
				display: block;
				transition: 1.4s ease-in-out;
				transform-origin: 50% 100%; } }
		&:hover {
			.services-block__image {
				img {
					transform: scale(1.2); } }
			h6 {
				color: $orange; } } }
	.swiper-slide {
		opacity: 0.3;
		h6 {
			opacity: 0; } }

	.swiper-slide-visible {
		opacity: 1;
		h6 {
			opacity: 1; } }

	h2 {
		margin-top: 0; }
	&__top {
		display: flex; }
	&__desc {
		margin-left: 170px;
		max-width: 340px;
		p {
			&:first-child {
				margin-top: 14px; }
			&:last-child {
				margin-bottom: 0; } } }

	@include bp(1550px) {
		&-slider {
			min-width: 0;
			width: 95%;
			margin-left: 0;

			img {
				width: 100%; } } }

	@include bp($point-4) {
		padding: 40px 0;
		&__top {
			display: block; }
		&__desc {
			margin-left: 0;
			max-width: 547px; }
		.navigation-arrows {
			display: none; }

		&-slider {
			margin-top: 30px; } }

	@include bp($point-2) {
		padding: 30px 0;
		&-slider {
			width: 84%; }

		.swiper-slide {
			opacity: 1; } } }


.banner {
	padding: 55px 0 45px;
	&__inner {
		width: 100%;
		padding: 130px 55px 131px;
		@include foil;
		color: $white;
		background-image: url(../images/foil-2.jpg); }
	&__part {
		display: flex;
		justify-content: space-between;
		align-items: center;
		max-width: 1450px; }
	&__desc {
		max-width: 582px;
		h2 {
			margin: 0 0 60px; } }

	@include bp($point-4) {
		padding: 40px 0;
		margin: 0 -55px;

		&__part {
			display: block; }

		&__inner {
			padding-bottom: 0;
			background-position: 28% 50%; }
		&__image {
			margin-top: 93px;

			img {
				margin-bottom: -55px; } } }

	@include bp(700px) {
		margin: 0 -20px; }

	@include bp($point-2) {
		padding: 15px 0 30px;
		&__desc {
			h2 {
				max-width: 218px;
				margin-bottom: 35px; } }
		.btn {
			width: auto; }

		&__inner {
			padding-top: 70px;
			padding-left: 40px;
			padding-right: 40px; }
		&__image {
			margin-top: 40px; } } }



.architecture {
	padding: 45px 0 110px;
	&__part {
		display: flex;
		justify-content: space-between;
		&--top {
			padding-bottom: 30px; } }
	&__block {
		width: 50%;

		&:nth-child(2) {
			width: 41.3%; }

		h2 {
			margin-top: 0; }

		h6 {
			color: $orange;
			margin-top: 0;
			margin-bottom: 35px; }

		.left-indent {
			padding-left: 22%; }

		p {
			max-width: 414px; } }

	@include bp(1400px) {
		&__block {
			.left-indent {
				padding-left: 0px; } } }

	@include bp($point-4) {
		padding: 90px 0 40px;
		&__block {
			width: 100%;
			margin-bottom: 35px;
			&:nth-child(2) {
				width: 100%; }

			h6 {
				margin-bottom: 20px; } }
		&__part {
			display: block;
			padding-bottom: 0; }
		&__image {
			img {
				width: 100%; } } }
	@include bp($point-2) {
		padding-bottom: 30px; } }

.rest {
	padding: 50px 0;
	&-block {
		&__part {
			width: 100%;
			display: flex;
			justify-content: space-between; }
		&__desc {
			max-width: 476px;
			margin-right: 30px;
			padding-top: 300px;

			h4 {
				color: $orange; }

			@include bp(961px,'min') {
				h4 {
					transform: translateX(100px);
					transition: 0.4s; }
				p {
					transform: translateX(50px);
					transition: 0.4s;
					transition-delay: 0.3s; } } }
		&__image {
			overflow: hidden;
			@include bp(961px,'min') {
				img {
					transition: 1.4s;
					transform: scale(1.1); } } } }

	&__inner {
		position: relative; }

	&__title {
		position: absolute;
		top: 140px;
		left: 0;
		h2 {
			margin-top: 0; } }
	&-slider {
		.navigation-arrows {
			position: absolute;
			top: 230px;
			left: 0;
			z-index: 3; }

		@include bp(961px,'min') {
			.swiper-slide {
				opacity: 0 !important;
				transition: 1s; }
			.swiper-slide-active {
				opacity: 1 !important; } }


		.swiper-slide-active {
			.rest-block {
				&__desc {
					h4,p {
						transform: translateX(0); } }

				&__image {
					img {
						transform: translateY(0); } } } } }

	@include bp($point-5) {
		&__title {
			top: 80px; }
		&-slider {
			.navigation-arrows {
				top: 160px; } }
		&-block {
			&__desc {
				padding-top: 200px; } } }

	@include bp($point-4) {
		padding-bottom: 25px;

		&__title {
			position: static;
			margin-bottom: 40px; }
		&-slider {
			width: 65%;
			margin-left: 0;
			overflow: visible;
			.navigation-arrows {
				display: none; } }
		&-block {
			&__part {
				flex-direction: column-reverse; }
			&__desc {
				padding-top: 0px;
				margin-right: 0;
				h4 {
					margin-top: 40px; } } } }

	@include bp($point-2) {
		padding-top: 35px;
		padding-bottom: 15px;
		&__title {
			margin-bottom: 27px; }
		&-slider {
			width: 80%; }
		&-block {
			&__desc {
				h4 {
					margin: 25px 0 15px; } } } } }



.spa {
	padding: 50px 0;
	.wrapper {
		padding: 0 20px 0 0;
		max-width: 1748px;
		margin-left: 0; }
	&__part {
		display: flex; }
	&__image--right,
	&-desc {
		margin-top: 140px; }
	&-desc {
		margin-left: 86px;
		max-width: 405px;
		padding-top: 15px;
		h2 {
			max-width: 272px; } }
	&__image {
		margin-right: 10%;
		&--right {
			margin-left: auto;
			margin-right: 0; } }

	@include bp($point-5) {
		&__image {
			margin-right: 5%;
			&--right {
				margin-right: 0; } }
		&-desc {
			margin-left: 40px; } }

	@include bp($point-4) {
		padding: 35px 0 40px;
		.wrapper {
			padding-right: 0; }
		&__part {
			flex-wrap: wrap; }
		.spa-desc {
			margin-left: 0;
			padding-left: 50px;
			order: 1;
			margin-top: 0;
			width: 100%;
			max-width: 100%;
			margin-bottom: 30px;
			p,ul,ol {
				max-width: 405px; }
			h2 {
				margin-top: 0; } }
		&__image {
			width: calc(50% - 35px);
			margin-right: 35px;
			order: 2;
			&--right {
				margin-right: 0px;
				margin-left: 35px;
				order: 3;
				margin-top: 90px; } } }

	@include bp(700px) {
		padding: 25px 0 30px;
		.spa-desc {
			padding-left: 20px;
			padding-top: 0; } }

	@include bp($point-2) {
		.wrapper {
			padding: 0 20px; }
		.spa-desc {
			padding-left: 0;
			margin-bottom: 10px; }
		&__image {
			margin-top: 0;
			width: calc(50% - 10px);
			margin-right: 10px;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover; }
			&--right {
				margin-right: 0;
				margin-left: 10px; } } } }



.complex {
	padding: 50px 0;
	.wrapper {
		margin-left: auto;
