.login-block {
	&__login-card {
		margin-top: 32px; } }

.login-menu {
	color: red;
	@include flex_between_center;
	&__list {
		@include flex_between_center;
		margin: 0;
		padding: 0; }
	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 35px;
		margin-top: 0;
		margin-bottom: 0;
		margin-right: 45px;
		&:after {
			display: none; } }
	&__link {
		cursor: pointer;
		&--active {
			color: $black;
			&:hover {
				color: $black; }
			&:after {
				content: "";
				position: absolute;
				left: 0;
				bottom: 0;
				width: 100%;
				height: 3px;
				background-color: $orange; } } }
	&__exit {
		color: $gray4;
		@include flex_between_center;
		&:hover {
			border: none; }
		& svg {
			fill: $gray4;
			stroke: $gray4;
			width: 18px;
			height: 19px; } }
	&__exit-text {
		margin-top: 0;
		margin-bottom: 0;
		margin-right: 16px; } }

.login-card {
	@include flex_between_center;
	flex-wrap: wrap;
	padding: 32px 58px;
	border: solid 7px $orange;
	border-style: solid;
	border-width: 7px;
	border-image: url('../images/pattern.jpg') 7 repeat repeat;
	&__img-wrapper {
		height: 76px; }
	&__number-wrapper {
		@include flex_between_center;
		justify-content: space-around;
		flex-wrap: wrap;
		flex-grow: 1; }
	&__text {
		padding-left: 20px;
		padding-right: 20px;
		font-weight: 500;
		font-size: 18px;
		line-height: 160%;
		@include bp($point-4) {
			margin-bottom: 0; } }
	&__number {
		padding-left: 20px;
		padding-right: 20px;
		font-weight: bold;
		font-size: 30px;
		line-height: 120%;
		letter-spacing: -0.01em;
		@include bp($point-4) {
			margin-bottom: 0; } }
	&__time {
		font-weight: 500;
		font-size: 18px;
		line-height: 160%; }
	@include bp($point-4) {
		flex-direction: column;
		padding: 32px 28px; }
	@include bp($point-2) {
		padding: 32px 8px; } }
