.form-checkbox {
	position: relative;
	padding: 0 0 0 40px;
	user-select: none;
	display: block;
	cursor: pointer;
	font-size: 14px;
	line-height: 180%;

	&[disabled] {
		opacity: .5;
		pointer-events: none; }

	&__input {
		position: absolute;
		cursor: pointer;
		opacity: 0;
		width: 0;
		height: 0;

		&:checked ~ .form-checkbox__checkmark:after {
			opacity: 1; } }

	&__text {
		font-size: $checkbox-fs;
		color: $checkbox-color;
		line-height: 1;

		&--mailing {
			padding-right: 20px;
			font-weight: 500;
			font-size: 16px;
			line-height: 140%; } }

	&__checkmark {
		position: absolute;
		top: -1px;
		left: 0;
		border: $checkbox-bor-width solid $checkbox-bor-color;
		border-radius: $checkbox-bor-radius;
		background-color: $white;
		width: 24px;
		height: 24px;

		&:after {
			background: url("../images/check.png") 50% no-repeat;
			background-size: 19px 15px;
			width: 100%;
			height: 100%;
			content: '';
			opacity: 0;
			transition: opacity 150ms ease-in-out;
			position: absolute;
			top: -4px;
			left: 3px; } }

	&__marker {
		&--mailing {
			position: absolute;
			top: -7px;
			left: -90px;
			width: 68px;
			height: 34px;
			border-radius: 18px;
			border: solid 2px $gray2;
			background-color: $gray2;
			cursor: pointer;
			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 30px;
				height: 30px;
				border-radius: 50%;
				background-color: $white;
				transition: transform 0.3s ease; } } } }

