.about-top {
	padding-bottom: 20px;
	&__foil {
		width: 100%;
		height: 891px;
		@include foil;
		background-size: auto;

		@include bp(1850px) {
			background-size: cover; } }

	&__part {
		display: flex;
		h1 {
			position: relative;
			z-index: 2;
			max-width: 670px;
			margin-bottom: 0; }
		.video-content__block {
			padding-top: 90px;
			max-width: 920px;
			width: 100%; } }

	&__desc {
		max-width: 482px;
		margin-left: 90px;
		padding-top: 60px; }

	@include bp(1300px) {
		&__desc {
			margin-left: 30px;
			max-width: 30%; } }

	@include bp(1150px) {
		&__part {
			display: block;
			.video-content__block {
				width: auto;
				max-width: 100%; } }
		&__desc {
			max-width: 481px;
			padding-top: 0;
			margin-left: 0; } }

	@include bp($point-4) {
		&__part {
			h1 {
				font-size: 7.5vw; }
			.video-content__block {
				padding-top: 60px;
				padding-bottom: 0; } } }

	@include bp($point-3) {
		&__foil {
			height: 85vw; } }

	@include bp(650px) {
		padding-bottom: 0;
		&__part {
			display: block;
			.video-content__block {
				padding-top: 20px; }
			h1 {
				font-size: rem(32px); } } } }



.step {
	padding: 40px 0;
	&__part {
		display: flex;
		margin: 0 -15px;
		max-width: 1520px; }

	&-unit {
		width: 33.33%;
		width: 100%;
		padding: 0 15px;
		color: $gray4;

		&__wrap {
			max-width: 276px; }

		&:first-child {
			.step-unit__wrap {
				max-width: 250px; } } }
	@include bp($point-4) {
		padding: 20px 0 30px; }
	@include bp(650px) {
		padding-top: 0;
		&__part {
			display: block;
			padding-left: 35px;
			padding-top: 10px; }
		&-unit {
			margin-bottom: 33px;
			&:last-child {
				margin-bottom: 0; } } }

	@include bp($point-2) {
		padding-bottom: 25px;
		&-unit {
			max-width: 200px; } } }




.area {
	padding: 50px 0;
	&__foil {
		width: 100%;
		height: 800px;
		@include foil;
		color: $white;
		display: flex;
		align-items: center;
		background-size: auto;

		@include bp(1850px) {
			background-size: cover; }

		.title-large {
			max-width: 938px; } }

	.video-content__block {
		margin-left: auto;
		&:before {
			width: 45%; }
		&:after {
			content: '';
			width: 2000px;
			height: 100%;
			position: absolute;
			top: 0;
			left: 99%;
			background-color: $white; } }

	@include bp(1400px) {
		&__foil {
			height: 55vw; } }

	@include bp($point-5) {
		.video-content__block {
			width: 100%;
			&:after {
				display: none; } } }

	@include bp($point-4) {
		padding: 10px 0 30px;
		.video-content__block {
			padding-top: 50px; } }

	@include bp(700px) {
		&__foil {
			height: 65vw; } }

	@include bp(650px) {
		.video-content__block {
			padding-top: 20px; } } }

.eko {
	.wrapper {
		max-width: 1356px; }
	&__part {
		display: flex;
		padding: 50px 0;
		&--center {
			align-items: center; } }
	&__item {
		width: 48%;
		padding-left: 55px;
		&:first-child {
			width: 52%;
			padding-left: 0;
			padding-right: 55px; }

		&--left {
			padding-left: 0; } }
	&__forest {
		display: flex;
		position: relative; }
	&__desc {
		max-width: 460px;
		margin-left: 50px;
		h2 {
			margin-top: 0; }
		.link {
			margin-top: 30px; }

		&--relax {
			max-width: 328px; }
		&--left {
			margin-left: 0;
			padding-top: 25px;
			padding-bottom: 60px;
			p {
				max-width: 400px; } }


		&--forest {
			max-width: 429px;
			margin-bottom: 180px;
			margin-top: -100px;

			@include bp($point-5) {
				margin-bottom: 90px; } } }

	&__image {
		&--right {
			text-align: right; } }

	&__image-forest {
		width: 49.7vw;
		position: absolute;
		right: 50.5%;
		top: 9%;

		@include bp(1400px) {
			top: 20%; } }


	@include bp($point-5) {
		&__desc {
			margin-left: 0; } }

	@include bp($point-4) {
		padding: 30px 0;
		&__part {
			display: block;
			padding: 0px 0 40px;
			&--reverse {
				display: flex;
				flex-direction: column-reverse;
				padding-bottom: 0; }
			p {
				&:last-child {
					margin-bottom: 0; } } }
		&__forest {
			display: block; }
		&__item {
			width: 100%;
			padding: 0;
			&:first-child {
				width: 100%;
				padding: 0; } }
		&__desc {
			max-width: 100%;
			margin-top: 60px;
			padding-top: 0;
			&--left {
				max-width: 465px;
				padding-bottom: 40px; }
			&--forest {
				margin-bottom: 40px; }
			&--fly {
				margin-bottom: 40px; } }

		&__image,
		&__image-forest {
			width: 100%;
			img {
				width: 100%; } }

		&__image-forest {
			position: static;
			margin-bottom: 30px; } }

	@include bp($point-2) {
		padding-top: 10px;
		&__desc {
			margin-top: 30px;
			.link {
				margin-top: 0px; }
			&--left {
				padding-bottom: 20px; }
			&--forest,
			&--fly {
				margin-bottom: 15px; } }
		&__part {
			padding-bottom: 15px; }
		&__image-forest {
			margin-bottom: 15px; } } }


.about-complex {
	padding: 60px 0;
	&__block {
		max-width: 1520px;
		margin-left: auto; }
	&__desc {
		max-width: 737px;
		margin-bottom: 100px;
		margin-left: 70px;
		h2 {
			margin-top: 0; }
		p {
			max-width: 436px; } }
	&__part {
		padding-left: 50.5%;
		position: relative; }

	&__image {

		&--2 {
			margin-bottom: 135px;
			img {
				margin-top: -140px; } }


		&--pos {
			position: absolute;
			left: 0;
			top: 0;
			width: 42%; } }

	@include bp($point-5) {
		&__desc {
			margin-left: 0; } }

	@include bp($point-4) {
		padding: 30px 0;

		&__part {
			padding: 0; }
		&__image {
			img {
				width: 100%; }
			&--2 {
				margin-bottom: 30px;
				img {
					margin: 0; } }
			&--pos {
				width: 100%;
				position: static;
				margin-bottom: 30px; } }

		&__desc {
			margin-bottom: 40px;
			max-width: 100%;
			p {
				max-width: 100%; } } }

	@include bp($point-2) {
		padding: 20px 0;
		&__image {
			&--2,
			&--pos {
				margin-bottom: 15px; } }
		&__desc {
			margin-bottom: 20px; } } }

.virtual-tour {
	padding: 50px 0;
	iframe {
		width: 100%;
		height: 90vh; }
	h2 {
		margin-top: 0;
		display: none; }

	@include bp($point-5) {
		iframe {
			height: 60vw; } }

	@include bp($point-4) {
		padding: 25px 0;

		h2 {
			display: block; } }

	@include bp($point-2) {
		iframe {
			height: 80vw; } } }


.relax {
	padding: 60px 0 40px;
	&__top {
		max-width: 928px;
		padding-bottom: 40px;
		h4 {
			margin-top: 0; } }
	&__line {
		display: flex;
		align-items: flex-start;

		h2 {
			margin-top: 0; }

		a {
			margin-left: 50px;
			margin-top: 32px; } }

	&-slider {
		overflow: visible;
		margin-right: -100px;

		.navigation-arrows {
			width: 42%;
			@include center('v');
			left: 35px;
			z-index: 3;

			&__item {
				&.swiper-button-disabled {
					opacity: 0; } } } }

	@include bp(1500px) {
		&-slider {
			margin-right: 0; } }

	@include bp($point-4) {
		padding: 40px 0 20px;
		&-slider {
			max-width: 645px;
			margin-left: 0;

			.navigation-arrows {
				display: none; } } }

	@include bp(700px) {
		padding: 25px 0;
		&-slider {
			max-width: 85%; }
		&__line {
			display: block;
			margin-bottom: 20px;
			h2 {
				margin-bottom: 5px; }
			a {
				margin: 0;
				font-size: rem(14px); } }
		&__top {
			padding-bottom: 0; } } }
