.shooting {
    &__types {
        margin: 80px -10px 0; }
    &__type {
        position: relative;
        &-block {
            padding: 10px 20px;
            @include bp(968px) {
                width: 100%; } }
        &-desc {
            position: absolute;
            bottom: 0;
            left: 0;
            padding: 50px;
            width: 100%;
            @include bp(1140px) {
                padding: 40px; }
            h3 {
                color: $white;
                margin-bottom: 35px;
                @include bp(540px) {
                    margin-bottom: 25px;
                    line-height: 100%; }
                @include bp(320px) {
                    margin-bottom: 15px; } }
            .btn {
                padding: 0 55px;
                @include bp(480px) {
                    padding: 0 40px;
                    width: auto; } } } }


    &__rules {
        margin: 60px -15px 0; }
    &__rule {
        display: block;
        background: $gray1;
        padding: 59px 45px 33px;
        height: 100%;
        color: $baseTxt;
        @include bp(1140px) {
            padding: 35px 35px 30px; }
        &-block {
            padding: 15px;
            @include bp(890px) {
                width: 50%; }
            @include bp(560px) {
                width: 100%; } }
        svg {
            width: 40px;
            height: 40px;
            display: inline-block; }
        svg:nth-child(2) {
            width: 21px;
            height: 19px; }
        p {
            line-height: 22.4px;
            max-width: 220px; } }
    &__room {
        margin: 65px 0;
        &-block {
            position: relative;
            overflow: hidden;
            color: $white; }
        &-img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            img {
                object-fit: cover;
                object-position: 60%;
                height: 100%; } }
        &-desc {
            position: relative;
            padding: 50px 50px 70px 50px;
            max-width: 600px;
            @include bp(768px) {
                padding: 40px 40px 60px 40px; }
            @include bp(490px) {
                padding: 30px 30px 50px 30px; } }
        h3 {
            margin: 0 0 20px; }
        p {
            line-height: 22.4px;
            font-size: 16px;
            margin-bottom: 30px; } } }




